.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}

.row {
    width: 80%;
    display: flex;
    text-align: left;
    justify-content: space-evenly;
}

.row:nth-child(1) {
    background-color: aqua;
}